import {AppGridConfig, PartialAppGridConfig} from '@/components/grid';
import deepMerge from 'ts-deepmerge';

export function pickGridColumns<T extends object>(
  source: AppGridConfig<T>,
  columnNames: Array<keyof T>,
  overrides?: PartialAppGridConfig<T>,
): AppGridConfig<T> {
  const columns: Partial<Record<keyof T, any>> = {};
  const labels: Partial<Record<keyof T, string>> = {};

  for (const name of columnNames) {
    columns[name] = source.columns[name];
    labels[name] = source.labels[name];
  }

  return deepMerge(
    {
      ...source,
      columns,
      labels,
    },
    overrides ?? {},
  ) as any as AppGridConfig<T>;
}
